export default {
    data() {
        return {
            lucky_money_balance: '--'
        }
    },
    methods: {
        topup() {
            this.$router.push({
                path: '/topup'
            });
        },
        refund() {
            this.$http({
                method:'post',
                url: `${BASE_URL}/lock-account/payment?refund=true`,
                data: {}
            }).then(() => {
                USER.isPaidDeposit = false;
                this.$vux.toast.show({
                    text: '成功提交退押金请求'
                });
                this.fetchData();
            });
        },
        fetchData() {
            const token = USER.token;
            if (token) {
                this.$http({
                    url: `${BASE_URL}/lock-account/balance`,
                }).then(function(result) {
                    const data = result.data;
                    if (data.status == 0) {
                        this.lucky_money_balance = data.lucky_money_balance;
                    }
                }.bind(this));
            }
        }
    },
    mounted() {
        this.fetchData();
    }
}



